<template>
  <div :class="$root.themeHomeChange === '1' ? 'supervisionPlanExecution_light' : 'supervisionPlanExecution'">
    <div class="header">
      <span class="left">监督计划完成情况统计</span>
      <div class="right">
        <el-date-picker
          v-model="yearData"
          type="year"
          placeholder="选择年"
          @change="getSupervisionComTable"
          style="margin-right: 15px;"
        >
        </el-date-picker>
        <el-button
          type="primary"
          :icon="dataType?'el-icon-s-grid':'el-icon-s-data'"
          @click="changeData"
        > {{dataType?'查看图表数据':'查看表格数据'}} </el-button>
      </div>
    </div>
    <el-form
        v-show="dataType"
      :inline="true"
      class="selectBox"
      :model="params">
      <!-- <el-button type="primary" style="float: left;">生成报告</el-button> -->
      <span>
        <el-form-item label="单位名称: ">
             <SelectTree
                 style="width: 100%"
                 ref="selectTreeRef"
                 :props="defaultProps"
                 :options="formOption"
                 :value="params.dwmc"
                 :clearable="true"
                 :accordion="true"
                 @getValue="getValue($event)"
             />
        </el-form-item>
      </span>
      <span>
        <el-form-item>
          <bda-search-input
            v-model="params.keyword"
            placeholder="请输入关键字"
            @search="getSupervisionComTable()"
            @clear="getSupervisionComTable()"
            size="mini"
          >
          </bda-search-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="getSupervisionComTable"
          > 查询 </el-button>
        </el-form-item>
           <el-form-item>
          <el-button
              type="primary"
              @click="excelExports"
          > 导出 </el-button>
        </el-form-item>
      </span>
    </el-form>
    <div style="height: calc(100% - 126px)" class="content" v-show="dataType">
      <element-bda-table-page
          v-loading="tableLoading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          :tableData="tableData"
          :columnArray="majorCol"
          :pageSize = pagesRs.pageSize
          :pageNo = pagesRs.pageNo
          :total = pagesRs.total
          @getPageNo="changePageNo"
          @getPageSize="changePageSize">
      </element-bda-table-page>
    </div>
    <div style="height: calc(100% - 66px)" v-show="!dataType" class="chartsData">
      <supervision-plan-statistics-charts ref="initCharts"></supervision-plan-statistics-charts>
    </div>
  </div>
</template>

<script>
  import Interface from "@/base/mixin/risk-control-center.js";
  import elementBdaTablePage from '@/components/table/element-bda-table-page'
  import supervisionPlanStatisticsCharts from "./supervisionPlanStatisticsCharts";
  import SelectTree from "@/views/components/orgTreeSelect";
  import { API_PATH } from '@/config'
  import axios from "axios";
  export default {
    name: "supervisionPlanExecution",
    mixins: [Interface],
    props: {},
    components: { elementBdaTablePage,supervisionPlanStatisticsCharts,SelectTree },
    data(){
      return {
        dataType:true,
        tableLoading: false,
        departmentList: [],
        showAdd: false,
        // 单位选择 变量
        defaultProps: {
          children: "list",
          label: "orgName",
          value: "orgId",
        },
        unitName:'',
        formOption:[],
        majorCol:[
          {prop: 'dwmc', label: '单位名称'},
          {prop: 'jdjhCount', label: '监督计划（条）',},
          {prop: 'completeProgressRate', label: '完成进度',},
          {prop: 'jdjcCount', label: '监督检查次数'},
          {prop: 'jdfxwtCount', label: '监督发现问题量'},
          {prop: 'wtzgRate', label: '问题整改情况'},
          {prop: 'dcjcfxwtCount', label: '单次检查发现问题数'},
          {prop: 'aqjglyCount', label: '安全监管领域'},
          {prop: 'scyxlyCount', label: '市场营销领域'},
          {prop: 'gcjslyCount', label: '工程建设领域'},
          {prop: 'ddjbgzhlyCount', label: '大党建办公室领域'},
          {prop: 'cwbxlyCount', label: '财务报销领域'},
          {prop: 'cxyszhlyCount', label: '创新与数字化领域'},
          {prop: 'otherCount', label: '其他'},
          {prop: 'wtsjlyCount', label: '问题涉及领域'}],
        tableData: [],

        params:{
          dwmc:'',
          keyword:'',
        },
        //表格
        yearData:'',
        pagesRs:{
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
      }
    },
    created() {
      this.init();
    },
    // 监听
    watch: {},
    mounted() {
      this.getSupervisionComTable()
      this.getOrgData()
    },
    methods: {
      init() {
        //获取年份
        this.yearData = new Date().getFullYear().toString();
      },
      //导出excel
      excelExports() {
        let path = '/supervision/chart/export'
        let data = {
          unit:this.unitName,
          keyword:this.params.keyword,
          year:this.yearData,
        }
        axios({
          method: "POST",
          url: API_PATH + path,
          data:data,
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          responseType: 'blob'
        }).then(res => {
          const content = res.data
          const blob = new Blob([content])
          const fileName = '监督计划执行情况图表.xls'
          if ('download' in document.createElement('a')) { // 非IE下载
            const elink = document.createElement('a')
            elink.download = fileName
            elink.style.display = 'none'
            elink.href = URL.createObjectURL(blob)
            document.body.appendChild(elink)
            elink.click()
            URL.revokeObjectURL(elink.href) // 释放URL 对象
            document.body.removeChild(elink)
          } else { // IE10+下载
            navigator.msSaveBlob(blob, fileName)
          }
        }).finally(()=>{
        }).catch(()=>{
        })
      },
      // 查询单位名称
      getOrgData() {
        this.getSupervisionUnitTree().then((rs)=>{
          // window.console.log(rs)
          if(Array.isArray(rs) && rs.length !== 0) {
            // window.console.log(rs)
            this.formOption = rs;
          } else {
            this.formOption = [];
          }
        }).finally(()=>{});
      },
      // 取值
      getValue(item){
        // window.console.log(item)
        this.unitName = item.length>0?item[1]:'';
        // this.queryTableList(true)
      },
      // 查询
      getSupervisionComTable() {
        this.tableLoading = true
        this.supervisionComTable({
          // codePrefix:this.params.dwmc,
          unit:this.unitName,
          current:this.pagesRs.pageNo,
          keyword:this.params.keyword,
          size:this.pagesRs.pageSize,
          year:this.yearData,
        }).then((rs)=>{
          window.console.log(rs)
          if(rs) {
            this.tableData = rs.rows;
            this.pagesRs.total = rs.records
          } else {
            this.tableData = [];
            this.pagesRs.total = 0
          }
        }).finally(()=>{
          this.tableLoading = false
        })

        this.$refs.initCharts.init()
      },
      // 查询表格内容
      queryTableList() {
      },
      changePageNo(pageNo){
        this.pagesRs.pageNo = pageNo
        this.getSupervisionComTable()
      },
      changePageSize(size){
        this.pagesRs.pageSize = size
        this.getSupervisionComTable()
      },
      changeData() {
        this.dataType = !this.dataType;
      }
    }
  }
</script>

<style scoped lang="scss">
  .supervisionPlanExecution {
    width: auto;
    height: calc(100% - 20px);
    padding: 5px 15px 15px 15px;
    .chartsData{
      height: 100%;
      width: 100%;
    }
    /deep/ {
      // 修改表格操作按钮样式
      .el-button--text {
        font-family: MicrosoftYaHei;
        font-size: 10px;
        color: #66B9FF;
        font-weight: 400;
      }
      .el-form {
        .el-form-item {
          // margin-bottom: 0px;
        }
        input::-webkit-input-placeholder {
          color: #4298F3;
        }

        input::-moz-placeholder {
          /* Mozilla Firefox 19+ */
          color: #4298F3;
        }

        input:-moz-placeholder {
          /* Mozilla Firefox 4 to 18 */
          color: #4298F3;
        }

        input:-ms-input-placeholder {
          /* Internet Explorer 10-11 */
          color: #4298F3;
        }
      }

      .el-button {
        border-radius: 5px!important;
      }

      // 修改按钮样式
      .el-button--primary {
        border-radius: 5px;
      }
      .el-button--info {
        color: #666;
        background: rgba($color: #eee, $alpha: 1);
        border: 1px solid rgba($color: #eee, $alpha: 1);
        border-radius: 5px;
      }

      .el-table__cell {
        border-left:  1px solid rgba(0, 0, 0, 1.0);
        border-right:  -1px solid rgba(0, 0, 0, 1.0);
      }
    }
    .header {
      width: calc(100% - 24px);
      text-align: left;
      font-family: MicrosoftYaHei-Bold;
      font-size: 14px;
      color: #FFFFFF;
      font-weight: bold;
      border-left: 4px solid #4298F3;
      padding: 2px 10px;
      margin: 15px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left {
        width: 150px;
      }
      .right {
        width: calc(100% - 150px);
        text-align: right;
      }
    }
    .selectBox {
      min-height: 40px;
      margin: 5px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      /deep/ .el-input__icon {
        color: #4298F3;
      }
      /deep/ .el-input__inner {
        background-color: #041338;
        color: #4298F3;
        border: 1px solid #2a5cb0;
        border-radius: 3px;
        text-align: left;
      }
      /deep/ .el-form-item__label {
        color: #FFF
      }
      
      // 修改时间组件样式
      /deep/ .el-range-editor--mini.el-input__inner {
        background-color: #041338;
        border: 1px solid #509be1;
        border-radius: 5px;
        .el-range-input, .el-range-separator {
          background-color: #041338;
          color: #FFF;
        }
      }
    }
    .content {
      width: 100%;
    }
  }
  
  // 浅色主题
  .supervisionPlanExecution_light {
    width: auto;
    height: calc(100% - 20px);
    padding: 5px 15px 15px 15px;
    /deep/ {
      // 修改表格操作按钮样式
      .el-button--text {
        font-family: MicrosoftYaHei;
        font-size: 10px;
        color: #66B9FF;
        font-weight: 400;
      }
      .el-form {
        .el-form-item {
          // margin-bottom: 0px;
        }
        input::-webkit-input-placeholder {
          color: #bbb;
        }

        input::-moz-placeholder {
          /* Mozilla Firefox 19+ */
          color: #bbb;
        }

        input:-moz-placeholder {
          /* Mozilla Firefox 4 to 18 */
          color: #bbb;
        }

        input:-ms-input-placeholder {
          /* Internet Explorer 10-11 */
          color: #ccc;
        }
      }

      .el-button {
        border-radius: 5px!important;
      }

      // 修改按钮样式
      .el-button--primary {
        color: #FFF;
        background: rgba($color: #409EFF, $alpha: .8);
        border: 1px solid rgba($color: #409EFF, $alpha: 1);
        border-radius: 5px;
      }
      .el-button--info {
        color: #666;
        background: rgba($color: #ddd, $alpha: 1);
        border: 1px solid rgba($color: #ddd, $alpha: 1);
        border-radius: 5px;
      }
    }
    .header {
      width: calc(100% - 24px);
