      font-family: MicrosoftYaHei-Bold;
      font-size: 14px;
      color: #333;
      font-weight: bold;
      border-left: 4px solid #4298F3;
      padding: 2px 10px;
      margin: 15px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left {
        width: 150px;
      }
      .right {
        width: calc(100% - 150px);
        text-align: right;
      }
    }
    .selectBox {
      min-height: 40px;
      margin: 5px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      /deep/ .el-input__icon {
        color: #bbb;
      }
      /deep/ .el-input__inner {
        background-color: #fff;
        color: #888;
        border: 1px solid #ccc;
        border-radius: 3px;
        text-align: left;
      }
      /deep/ .el-form-item__label {
        color: #333;
      }

      // 修改时间组件样式
      /deep/ .el-range-editor--mini.el-input__inner {
        background-color: #041338;
        border: 1px solid #509be1;
        border-radius: 5px;
        .el-range-input, .el-range-separator {
          background-color: #041338;
          color: #FFF;
        }
      }
    }
    .content {
      width: 100%;
    }
  }
</style>
